import {ChampionModel} from './models/championModels'

const championModel = new ChampionModel();
championModel.loadChampions();

async function setImage() {
  //  console.log(championModel)

    for (let i = 0; i < championModel.champions.length; i++) {
        try {
            champion = championModel.champions[i]
            // Puedes usar `champion.name` dinámicamente
            const response = await fetch(`https://ddragon.leagueoflegends.com/cdn/13.18.1/img/champion/${champion.id}.png`);
            
            if (response.ok) {
                const sprite = response.url; 
              //  console.log(sprite)
                champion.setImage(sprite);
             //   console.log(`Imagen de ${champion.name} cargada`);
            } else {
                console.error(`Error al cargar la imagen del campeón: ${champion.name}`);
            }
        } catch (error) {
            console.error(`Error en la petición de imagen para el campeón ${champion.name}:`, error);
        }
    }
 }


 const showContainer = async () => {
    const container = document.getElementById('Container'); 

    if (!container) {
        console.error('No se encontró el contenedor con ID "Container"');
        return;
    }

    container.innerHTML = '';
    console.log(championModel.champions)
    for (let i = 0; i < championModel.champions.length; i++) {
        const champion = championModel.champions[i];

        
        // console.log(champion.name , champion.image);
        console.log(typeof(championModel.champions[i].image))

        img = await fetch(`${championModel.champions[i].image}`)

        // console.log(img)

        // if (img.ok) {
        //     const sprite = response.url;
        //     console.log(sprite)

        


        const championCard = document.createElement("div");
        championCard.classList.add("card"); 

        championCard.innerHTML = `
            <p>${champion.name}</p>
            <img class="champion_image" src=${champion.image} alt=${champion.name}>
        `;

        container.appendChild(championCard);

    }

};




document.addEventListener('DOMContentLoaded', () => {
      setImage();
      showContainer();
   
});
